import {
	getEvaluatesByUid
} from '../../api/evaluate.js'
export default {
	data() {
		return {
			data: []
		}
	},
	onShow() {
		if (uni.getStorageSync('token')) {
			this.getAllEvaluates();
		}
	},
	methods: {
		getAllEvaluates() {
			getEvaluatesByUid().then(data => {
				this.data = data.data;
			}).catch(err => {})
		}
	}
}
